import Vue from 'vue';
import Router from 'vue-router';

// async load component
const Home = () => import('@/pages/home');
const Share = () => import('@/pages/share');
const newWork = () => import('@/pages/new/work');

Vue.use(Router);

export default new Router({
  routes: [{
    path: '/',
    name: 'home',
    component: Home,
  }, {
    path: '/share',
    name: 'share',
    component: Share
  }, {
    path: '/newWork',
    name: 'new-work',
    component: newWork,
  }],
});



